import { Component, OnInit } from '@angular/core';
import { NavController,MenuController } from '@ionic/angular';
@Component({
  selector: 'app-mysettings',
  templateUrl: './mysettings.page.html',
  styleUrls: ['./mysettings.page.scss'],
})
export class MysettingsPage implements OnInit {

  constructor(
    public navCtrl: NavController,public menuCtrl:  MenuController) { }

  ngOnInit() {
  }

  navigateToMyProfile(){
    this.navCtrl.navigateForward("my-profile");
  }
  ionViewWillEnter() {
    this.menuCtrl.enable(false);
  }
  goBack(){
    this.navCtrl.navigateBack('dashboard')
  }
  signOut(){
        this.navCtrl.navigateRoot('get-started')
  }

}
